@font-face {
  font-display: swap;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/inter-v13-latin-regular.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/inter-v13-latin-500.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  src: url("../../fonts/inter-v13-latin-600.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: rgba(5, 5, 5, 0.8);
  background-color: #f8f8f8;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  display: inline-block;
  text-decoration: none;
}

address {
  font-style: normal;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 100px;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.btn-rating, .btn-add, .primary-btn {
  display: block;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  line-height: 1.21;
  font-weight: 500;
  padding: 13px 27px;
  border: 1px solid #9bb537;
  border-radius: 15px;
  cursor: pointer;
  transition: color 150ms ease, background-color 150ms ease;
}

.pag-page, .pag-arrow-next, .pag-arrow-prev, .pag-arrow-last, .pag-arrow-first {
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid;
}

.pag-arrow-last, .pag-arrow-first {
  display: flex;
}
.pag-arrow-last .svg, .pag-arrow-first .svg {
  flex-shrink: 0;
  margin: -8px;
  width: 24px;
  height: 24px;
  pointer-events: none;
}

.pag-arrow-next .svg, .pag-arrow-prev .svg {
  width: 24px;
  height: 24px;
  pointer-events: none;
}

.primary-btn {
  color: #050505;
  background-color: #f8f8f8;
}
.primary-btn:hover {
  color: #f8f8f8;
  background-color: #9bb537;
}

.btn-add {
  color: #f8f8f8;
  background-color: #9bb537;
}
.btn-add:hover {
  color: #050505;
  background-color: transparent;
}

.btn-rating {
  color: #050505;
  background-color: #f8f8f8;
}
.btn-rating:hover {
  color: #f8f8f8;
  background-color: #9bb537;
}

.pag-arrow-first {
  background-color: #050505;
}
.pag-arrow-first .svg {
  fill: rgba(248, 248, 248, 0.6);
}

.pag-arrow-prev {
  background-color: #050505;
}
.pag-arrow-prev .svg {
  fill: rgba(248, 248, 248, 0.6);
}

.pag-arrow-last {
  transform: rotate(180deg);
  background-color: #9bb537;
  border-color: #9bb537;
}
.pag-arrow-last .svg {
  fill: #050505;
}

.pag-arrow-next {
  transform: rotate(180deg);
  background-color: #9bb537;
  border-color: #9bb537;
}
.pag-arrow-next .svg {
  fill: #050505;
}

.pag-page {
  border-color: rgba(5, 5, 5, 0.3);
  font-family: "Inter", sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.22;
}
.pag-page.current {
  border-color: #9bb537;
  background-color: #9bb537;
}
.pag-page.hidden {
  display: none;
}

.hero-card .card-dish, .hero-card .card-chief {
  background-color: #050505;
  border-radius: 15px;
  overflow: hidden;
}

.dish-3-big-img, .dish-2-big-img, .dish-1-big-img {
  position: absolute;
  top: 50%;
  left: 60%;
}

.hero-card {
  display: flex;
  gap: 16px;
  height: 442px;
  width: 871px;
}
.hero-card .card-chief {
  position: relative;
  width: 137px;
}
.hero-card .card-dish {
  position: relative;
  width: 351px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  justify-content: end;
  padding-bottom: 32px;
}

.chief {
  position: absolute;
  top: 47%;
  left: 60%;
  transform: translate(-50%, -50%) scale(3.1);
}

.dish-img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 271px;
  z-index: 1;
}

.elips-img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.dish-1-big-img {
  transform: translate(-50%, -50%) scale(1.32);
}

.dish-2-big-img {
  transform: translate(-50%, -50%) scale(1.9);
}

.dish-3-big-img {
  top: 57%;
  transform: translate(-50%, -50%) scale(2.4);
}

.master-text {
  font-weight: 500;
  color: rgba(248, 248, 248, 0.6);
  text-transform: uppercase;
}

.master-text-country {
  color: rgba(248, 248, 248, 0.2);
  font-size: 12px;
  line-height: 1.16;
}

.search .input, .options-wrapper {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  color: rgba(5, 5, 5, 0.5);
  padding: 14px 18px;
  border: 1px solid rgba(5, 5, 5, 0.18);
  border-radius: 15px;
  width: 100%;
  appearance: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.area-list,
.ingredients-list,
.time-list {
  position: absolute;
  left: 0;
  top: 101%;
  width: 100%;
  height: 180px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 14px 18px;
  background-color: #f8f8f8;
  border-radius: 14px;
  box-shadow: 0px 4px 36px 0px rgba(0, 0, 0, 0.02);
  overflow-y: auto;
  z-index: 1;
  cursor: default;
}
.area-list::-webkit-scrollbar,
.ingredients-list::-webkit-scrollbar,
.time-list::-webkit-scrollbar {
  width: 6px;
  position: absolute;
  right: 60px;
}
.area-list::-webkit-scrollbar-thumb,
.ingredients-list::-webkit-scrollbar-thumb,
.time-list::-webkit-scrollbar-thumb {
  background-color: rgba(5, 5, 5, 0.18); /* color of the scroll thumb */
  border-radius: 12px; /* roundness of the scroll thumb */
}

.options-wrapper {
  position: relative;
  width: 100%;
  -moz-appearance: none;
  -webkit-appearance: none;
}

.filter {
  position: relative;
  display: flex;
  gap: 16px;
  margin-bottom: 44px;
}
.filter .arrow {
  position: absolute;
  right: 18px;
  top: 15px;
  width: 18px;
  height: 18px;
  fill: rgba(5, 5, 5, 0.5);
  transform: rotate(-90deg);
  pointer-events: none;
}

.filter-label {
  font-weight: 500;
  color: rgba(5, 5, 5, 0.5);
  display: block;
  text-align: start;
  margin-bottom: 8px;
}

.time {
  width: 125px;
}

.area {
  width: 141px;
}

.ingredients {
  width: 188px;
}

.search {
  position: relative;
  width: 278px;
}
.search .input {
  padding-left: 48px;
}

.loop {
  position: absolute;
  top: 41px;
  left: 18px;
  width: 18px;
  height: 18px;
  fill: rgba(5, 5, 5, 0.5);
}

.filter-btn {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  text-align: left;
  width: 95%;
  padding: 0;
  color: rgba(5, 5, 5, 0.3);
  background-color: transparent;
  border: none;
  text-overflow: ellipsis; /* enables ellipsis */
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
}
.filter-btn:hover {
  color: #050505;
}

.reset-filter-btn {
  position: absolute;
  bottom: -30px;
  right: 0;
  display: flex;
  align-items: center;
  gap: 4px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.reset-svg {
  width: 14px;
  height: 14px;
  fill: #9bb537;
}

.reset-text {
  color: rgba(5, 5, 5, 0.5);
  font-family: "Inter", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  width: 86px;
}

.recipe-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: end;
  width: 250px;
  height: 287px;
  border-radius: 15px;
  overflow: hidden;
  background: linear-gradient(1deg, rgba(5, 5, 5, 0.6) 4.82%, rgba(5, 5, 5, 0) 108.72%);
  padding: 16px;
}
.recipe-card .title {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28;
  text-transform: uppercase;
  color: #f8f8f8;
  margin-bottom: 8px;
  text-overflow: ellipsis; /* enables ellipsis */
  white-space: nowrap; /* keeps the text in a single line */
  overflow: hidden;
}
.recipe-card .text {
  font-size: 12px;
  line-height: 1.33;
  color: #f8f8f8;
  margin-bottom: 16px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; /* start showing ellipsis when 2rd line is reached */
}
.recipe-card .info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.recipe-img {
  position: absolute;
  top: 18px;
  left: 0;
  transform: scale(1.16);
  z-index: -1;
}

.heart {
  position: absolute;
  top: 16px;
  right: 16px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.heart-svg {
  width: 22px;
  height: 22px;
  stroke: #f8f8f8;
  fill: none;
}
.heart-svg:active {
  transform: scale(1.2);
}
.heart-svg.added {
  fill: #f8f8f8;
}

.heart-use {
  pointer-events: none;
}

.recipe-btn {
  font-family: "Inter", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.25;
  color: #f8f8f8;
  background-color: #9bb537;
  border: none;
  border-radius: 8px;
  padding: 8px 16px;
  cursor: pointer;
}

.score {
  display: flex;
  gap: 4px;
}

.score-text {
  font-weight: 500;
  color: #f8f8f8;
}

.stars {
  display: flex;
  gap: 2px;
}

.star-svg {
  width: 16px;
  height: 16px;
  fill: rgba(5, 5, 5, 0.1);
}
.star-svg.active {
  fill: #eea10c;
}

.header {
  padding-top: 43px;
  padding-bottom: 50px;
}
.header .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-nav {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
}

.nav-list {
  display: flex;
  gap: 16px;
}
.nav-list .link {
  font-size: 12px;
  line-height: 1.2;
  color: #050505;
  transition: color 150ms ease;
}
.nav-list .link:hover, .nav-list .link:active {
  color: #9bb537;
}
.nav-list .link.current {
  color: #9bb537;
}

.nav-logo {
  display: flex;
  justify-content: center;
  flex-grow: 1;
}
.nav-logo .src {
  width: 93px;
}

.nav-tech {
  display: flex;
  gap: 24px;
}

.cart-svg {
  width: 24px;
  height: 24px;
  fill: #050505;
}

.theme-wrapper {
  display: flex;
  justify-content: start;
  align-items: center;
  width: 46px;
  height: 20px;
  background-color: rgba(5, 5, 5, 0.3);
  border-radius: 11px;
  padding: 1px;
  box-shadow: 4px 4px 8px rgba(5, 5, 5, 0.3);
}

.theme-circle {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #f8f8f8;
}

.recipes {
  padding-top: 184px;
  padding-bottom: 100px;
}
.recipes .wrapper {
  display: flex;
  justify-content: space-between;
  gap: 64px;
}

.aside {
  padding-top: 26px;
  width: 234px;
  flex-shrink: 0;
}

.recipe-list {
  display: flex;
  flex-wrap: wrap;
  row-gap: 20px;
  column-gap: 16px;
}

.all-recipes {
  margin-bottom: 64px;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(5, 5, 5, 0.18);
  backdrop-filter: blur(2px);
  z-index: 1;
}

.recipe-modal {
  position: relative;
  padding: 32px;
  background-color: #f8f8f8;
  border-radius: 15px;
}
.recipe-modal .title {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.22;
  text-transform: uppercase;
  margin-bottom: 16px;
  color: #050505;
}
.recipe-modal .video {
  width: 467px;
  height: 250px;
  border-radius: 8px;
  margin-bottom: 16px;
}
.recipe-modal .text {
  width: 467px;
  margin-bottom: 32px;
}
.recipe-modal .buttons {
  display: flex;
  gap: 16px;
}

.tag-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;
  align-items: center;
}

.tag-list {
  display: flex;
  gap: 4px;
}
.tag-list .item {
  padding: 8px 16px;
  border-radius: 35px;
  border: 1px solid rgba(5, 5, 5, 0.18);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
  color: #050505;
}

.modal-score-text {
  margin-left: 8px;
  font-weight: 500;
  color: rgba(5, 5, 5, 0.5);
}

.ing-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 32px;
  height: 204px;
  overflow-y: auto;
}
.ing-list::-webkit-scrollbar {
  width: 2px; /* width of the entire scrollbar */
}
.ing-list::-webkit-scrollbar-thumb {
  background-color: rgba(5, 5, 5, 0.18); /* color of the scroll thumb */
  border-radius: 12px; /* roundness of the scroll thumb */
}
.ing-list .item {
  display: flex;
  justify-content: space-between;
  padding-bottom: 6px;
  border-bottom: 1px solid rgba(5, 5, 5, 0.18);
  color: #050505;
}
.ing-list .item .count {
  color: rgba(5, 5, 5, 0.5);
}

.btn-close {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.numbers {
  display: flex;
  gap: 10px;
}

.pages {
  display: flex;
  gap: 10px;
}

.steps {
  display: flex;
  gap: 8px;
}

.pagination-recipe {
  display: flex;
  justify-content: center;
  gap: 24px;
}

.error {
  display: flex;
  flex-direction: column;
  gap: 32px;
  align-items: center;
  margin: 50px auto;
}

.error-svg {
  width: 97px;
  height: 83px;
}

.error-text {
  width: 480px;
  color: #050505;
  text-align: center;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 1.5; /* 150% */
}